import React, { useState, useLayoutEffect } from 'react';
import { Helmet } from 'react-helmet';

import Nav from '../nav';

export const SEO = ({ seo_title, meta_description }) => {
  if(seo_title === undefined) seo_title = "Designworks";

  return (
    <Helmet defer={false}>
      <title>{seo_title}</title>
      <meta name="description" content={meta_description} />
    </Helmet>
  );
};

const Layout = ({ data, children, uri }) => {
  const [cursorItems, setCursorItems] = useState([]);
  
  if (data === null || data === undefined) return children;

  const entry = Object.values(data)[0];
  const seo = entry.seo;
  const splitUri = uri.split('/');
  const section = splitUri[2] === 'articles' ? 'article' : splitUri[1];

  useLayoutEffect(() => {
    setTimeout(() => {
      let cursors = document.querySelectorAll('.hover-cursor');

      if (cursors.length <= cursorItems.length) return;

      setCursorItems(cursors);
    }, 50);
  }, [cursorItems]);

  return (
    <>
      <SEO {...seo} />

      <main className={section === 'about' ? '-dark-theme' : ''}>
        <Nav />
        {children}
      </main>
    </>
  );
};

export default Layout;
